.slider {
  position: relative;
  width: 100%;
  overflow: hidden; }
  .slider__wrapper {
    overflow: hidden;
    position: relative;
    width: 100vw;
    height: 100%;
    z-index: 1; }
  .slider__slides {
    display: flex;
    position: relative;
    top: 0;
    left: -100vw;
    width: 400vw; }
    .slider__slides.shifting {
      transition: left 0.2s ease-out; }
  .slider__control {
    position: absolute;
    z-index: 2;
    content: "";
